'use client'

import { useState } from 'react'
import { useData } from '@/lib/data-context'
import { AuthLayout } from '@/components/auth-layout'
import { PageHeader } from '@/components/page-header'
import { Button } from '@/components/ui/button'
import { RequestWizard } from '@/components/requests/request-wizard'
import { useToast } from '@/hooks/use-toast'

export default function RequestsPage() {
  const { addRequest } = useData()
  const { toast } = useToast()
  const [isWizardOpen, setIsWizardOpen] = useState(false)

  const handleWizardSubmit = (requests: Array<{
    institution: string
    requiredDate: string
    status: 'pending' | 'approved' | 'delivered' | 'cancelled'
    items: Array<{
      materialId: string
      materialName: string
      quantity: number
      unitPrice: number
      total: number
    }>
    totalValue: number
  }>) => {
    // Criar uma requisição para cada instituição
    requests.forEach(requestData => {
      addRequest(requestData)
    })

    toast({
      title: 'Requisições criadas',
      description: `${requests.length} requisição(ões) criada(s) com sucesso.`,
    })
  }

  return (
    <AuthLayout>
      <div className="p-6 lg:p-8">
        <PageHeader
          title="Requisições de Materiais"
          description="Gerencie as requisições de saída de materiais das instituições"
          action={
            <Button 
              onClick={() => setIsWizardOpen(true)} 
              className="bg-primary hover:bg-primary/90 font-medium"
            >
              Nova Requisição
            </Button>
          }
        />
      </div>

      <RequestWizard
        isOpen={isWizardOpen}
        onClose={() => setIsWizardOpen(false)}
        onSubmit={handleWizardSubmit}
      />
    </AuthLayout>
  )
}
